@import 'src/styles/app';

.app {
  height: 100vh;

  background: $darkBlue;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;

  height: calc(100vh - 80px);
  max-width: $maxContainerWidth;
  margin: 0 auto;

  padding: 20px;
}

@media (max-width: $tabletBreakpoint) {
  .main {
    padding: 0;
  }
}